





















































































	/*次级选择列表*/
.main div.right_zero{
	height: 100%;
}
.main div.right_zero{
	height:100%;
}
.main .main_con div.table{
    margin-left: 10px;
    margin-bottom: 0px;
    height: calc(100% - 214px);
}
ul.MagOrg{
	list-style: none;
	margin:0px;
	padding:0px;
	color:#b9cdd7;
	width:95%;
	cursor:pointer;
	border-radius: 5px;
    overflow: hidden;
}
ul.MagOrg>li{
	background-color: #2a404e;
	margin-bottom:1px;
	line-height:2em;
}
ul.MagOrg>li p.active{
	background-color: #587081;
}
ul.childOrg{
/*	display: none;*/
	list-style: none;
	margin:0px;
	padding:0px;
	padding-left:32px;
	color:#b9cdd7;
	cursor:pointer;
	background-color:#223642;
}
ul.childOrg li{
	background-color:#223642;
}
ul.childOrg li.active{
	background-color:#587081;
}
ul.MagOrg li p {
	margin: 0px;
}
ul.MagOrg li span{
	display: block;
	float:left;
	height:16px;
	width:16px;
	margin-top:8px;
	background-size:contain;
	background-repeat: no-repeat;
	background-position: center;
}
ul.MagOrg li span.symbol{
	background-image: url(../../../assets/Manage/add.png);
	margin-left:1em;
}
ul.MagOrg li span.user{
	background-image: url(../../../assets/Manage/user.png);
}
ul.MagOrg li span.icon{
	background-image: url(../../../assets/Manage/to_right.png);
	height: 10px;
	width:10px;
	margin-top:12px;
}
.main .main_con div.table {
    padding: 10px;
    background-color: #1f2e3a;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: height 1s;
  transition: height 1s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  height: 0.5s
}



